.main-navigation,
.primary-menu
 {
	display: flex;
	justify-content: flex-start;

	ul {
		background-color: $color__link;
		color: white;
		margin: 0;
		padding-left: 0;
		list-style-type: none;
		letter-spacing: 0.2rem;
	
		@media screen and (min-width: $extra_large_screen) {
			display: flex;
			flex-direction: row;
			height: 100px;
			position:static;
			background-color: unset;
			color: $color__link;

		} 

		ul {
			display: flex;
			

			ul {
				left: -999em;
				top: 0;
			}

			li {

				&:hover > ul,
				&.focus > ul {
					display: block;
					left: auto;
				}
			}

			a {
				width: 200px;
				
			}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	li {
		position: relative;
		margin: 1rem;
		&:hover > a,
		&.focus > a {
		}
	}

	a {
		display: block;
		text-decoration: none;
		font-size: 0.8rem;
		color:white;
		@media screen and (min-width: $extra_large_screen) { 
			color: $color__link;
		}
		
	}

	a:hover {
		@media screen and (min-width: $extra_large_screen) { 
			color: $color__link-hover
		}

	}

 }

.comment-navigation,
.posts-navigation,
.post-navigation {
	max-width:900px; 
	margin: 3em auto;
	.site-main & {
	
	}

	.nav-links {
		display: flex;
		margin: 0 auto;
		font-size: 1.2rem;
		
	}

	.nav-previous {
		flex: 1 0 50%;
	}

	.nav-next {
		text-align: end;
		flex: 1 0 50%;
	}
	
}


.toggler {
    padding:auto 10px;
    display: flex;
    flex-direction:column;
	margin: 5px 5px;
	cursor: pointer;
    @media screen and (min-width: $extra_large_screen) {

        display: none;
    }    
    div {
        height: 2px;
        width: 30px;
        margin: 4px 0;
        background-color: black;
    }  
   
}  


.menu {
	position: absolute;
	left: 0px;
	top:0px; 
	display:flex; 	
	flex-direction: column; 
	height:100vh;
	width: 350px;
	background-color: $color__link;
	justify-content: center;
	align-items: center;
	transform: translateX(-350px);
	box-shadow: 0 1px 1px rgba(0,0,0,0.2);
	transition: transform 0.2s ease-in;



	@media screen and (min-width: $extra_large_screen) {
		box-shadow: unset;
		position: static;
		transform: translateX(0px);
		transform: translateY(2px);

    }    

}


.menu-active {
	display: flex;
	transform: translateX(0px);
	@media screen and (min-width: $extra_large_screen) {
		position: unset;

    }    

}




.nav-menu
 {
    display:flex; 
 }


.sub-menu {
    display:none;
 }

